import React, { useState, useEffect, useRef } from 'react'
import Blog from './components/Blog'
//import BlogForm from './components/BlogForm'
import LoginForm from './components/LoginForm'
import Notification from './components/Notification'
import CreateBlogForm from './components/CreateBlogForm'

import blogService from './services/blogs'
import loginService from './services/login'
import Togglable from './components/Togglable'

const App = () => {
  const [blogs, setBlogs] = useState([])
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [user, setUser] = useState(null)
  const [notification, setNotification] = useState('')
  const [tone, setTone] = useState('')


  useEffect(() => {
    blogService.getAll().then(blogs =>
      setBlogs( blogs )
    )
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogAppUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
      blogService.setToken(user.token)
    }
  }, [])

  const handleLogin = async (event) => {
    event.preventDefault()
    console.log(`logging in with ${username} ${password}`)
    try {
      const user = await loginService.login({ username, password })
      console.log('UUUUSERI')
      console.log(user)

      window.localStorage.setItem(
        'loggedBlogAppUser',
        JSON.stringify(user)
      )

      blogService.setToken(user.token)
      console.log('tokenin asetus onnistuu')
      setUser(user)
      setUsername('')
      setPassword('')
      console.log('kaikki onnistuu')
    } catch (exception) {
      console.log(exception)
      console.log('missing or invalid credentials')
      setTone('neg')
      setNotification('missing or invalid credentials')
      setTimeout(()  => {
        setNotification('')
      }, 5000)
    }
  }

  const handleLogOut = () => {
    //event.preventDefault()
    window.localStorage.removeItem('loggedBlogAppUser')
    setUser(null)
  }

  const createBlog = async (blogObject) => {
    blogFormRef.current.toggleVisibility()
    /*
    const blogObject = {
      title: title,
      author: author,
      url: url,
    }
    */
    console.log('LISÄTTÄVÄ BLOGI')
    console.log(blogObject)
    const returnedBlog = await blogService.create(blogObject)
    console.log('returned blogi')
    console.log(returnedBlog)
    setBlogs(blogs.concat(returnedBlog))

    setTone('pos')
    setNotification(`A new blog "${returnedBlog.title}" added`)
    setTimeout(() => {
      setNotification('')
    }, 5000)
  }

  const changeBlog = async (blogObject, id) => {
    const returnedBlog = await blogService.change(blogObject, id)

    setBlogs(
      blogs
        .map(b => (
          b.id !== returnedBlog.id ? b : returnedBlog
        )))
  }

  const removeBlog = async (id) => {
    await blogService.remove(id)

    setBlogs(
      blogs
        .filter(b => (
          b.id !== id
        )))
  }

  const loginForm = () => (
    <Togglable buttonLabel='Login'>
      <LoginForm
        handleLogin={handleLogin}
        username={username}
        password={password}
        setUsername={setUsername}
        setPassword={setPassword}
      />
    </Togglable>
  )

  const blogFormRef = useRef()

  const createBlogForm = () => (
    <Togglable buttonLabel='New blog' ref={blogFormRef}>
      <CreateBlogForm
        createBlog={createBlog}
      />
    </Togglable>
  )

  return (
    <div>
      <h1>BlogApp</h1>
      {notification !== ''
        && <Notification
          notification={notification}
          tone={tone}
        />
      }
      {user === null
        ? loginForm()
        : <div>
          <p>
            {user.name} is logged in
            <button onClick={handleLogOut}>
              Log out
            </button>
          </p>
          <div>
            <h2>blogs</h2>
            {createBlogForm()}
            {blogs
              .sort((a, b) => (
                b.likes - a.likes
              ))
              .map(blog =>
                <Blog
                  key={blog.id}
                  blog={blog}
                  changeBlog={changeBlog}
                  removeBlog={removeBlog}
                  loggedUsername={user.user}/>
              )}
          </div>
        </div>
      }
    </div>
  )
}

export default App

